<template >
  <div id="main" >
    <el-container class="reserve-container">
<!--      <这是最顶部>-->
      <el-header class="first-header" style="position: relative;width: 100%;height: 10%">
        <img src="../../static/logo.png" style="width: 20%;height: 10%;position: relative;left: 10%" >
        <div>
        <i class="el-icon-user-solid"  style="font-size: 35px;position: relative;right:300% "></i>
          <i class="el-icon-s-order" style="font-size: 35px;position: relative;right:200%"></i>
        </div>
      </el-header>
      <el-main>
<!--        <这是搜索部分>-->
      <el-header class="secend-header"    style="width: 50%; height: 30%; position: relative; left: 25%;">
           <div style="height: 100%;width: 100%;" >
               <img src="../../static/pexels-faik-akmd-1025469.jpg" style="height: 100%;width: 100%;left: 0px; top: 0px; position: absolute; border-radius: 30px; box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 0px;" >
             <span style="font-size:28px;line-height:1.3;position: relative;left:-40%;color: #f9fafc" >预订酒店</span>
              <div class="reserve-input1" style="position: relative;min-height: 20px;left: 0" >
               <el-input
                   v-model="formhotel.hotelname"
                   placeholder="酒店名称"
                   style="width: 35%;"
               >
               </el-input>
                <el-divider direction="vertical"></el-divider>
               <div style="height: 70px">
                 <el-date-picker
                     v-model="formhotel.timerange"
                     type="daterange"
                     start-placeholder="入住时间"
                     end-placeholder="退房时间"
                     :default-time="['00:00:00', '23:59:59']"
                     >
                 </el-date-picker>
               </div>
             </div>
             <div class="reserve-input2" style="position: relative;left:0%;min-height:0">
               <el-select v-model="formhotel.rating" placeholder="请选择" >
                 <el-option
                     v-for="item in options"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value"
                 >
                   <span style="float: left">{{ item.label }}</span>
                   <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                 </el-option>
               </el-select>
               <el-divider direction="vertical"></el-divider>
               <el-input
                   v-model="formhotel.keyword"
                   placeholder="关键词(选填)"
                   suffix-icon="el-icon-date"
                   style="width:25%"
                   >
               </el-input>
             </div>
             <el-button  @click="search(formhotel)" type="primary" icon="el-icon-search" style="color: white;background-color: rgb(201,124,72);position: relative;left: 40%">
               搜索
             </el-button>
           </div>
      </el-header>
      <el-main>
        <el-container class="Hotel-recommendation-container">
          <el-header class="third-header" style="display: flex">
            <div style="position: relative;left: 50%" >
              <span>酒店推荐</span>
            </div>
          </el-header>
          <el-main style="height: 500px">
            <div  class="hotel-list" style="display: flex; justify-content: space-between;position: relative;height: 100%;width: 70%; left: 10%">
              <div>
                <el-carousel indicator-position="outside" style="position: relative;width:800px;height:100%;left: 25%">
                  <el-carousel-item v-for="item in 4" :key="item">
                    <img src="../../static/hotel1.png" alt="" style="position: relative;width:100%;height:100%;">
                  </el-carousel-item>
                </el-carousel>
              </div>
            </div>
          </el-main>
        </el-container>
      </el-main>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import router from "@/router";


export default {
  name: "MainInterface",
  data(){
    return{
      options: [{
        value: '选项1',
        label: '五星'
      }, {
        value: '选项2',
        label: '四星'
      }, {
        value: '选项3',
        label: '三星及以下'
      }, ],
      formhotel: {
        hotelname: '',
        keyword: '',
        timerange: '',
        rating: ''
      }
    }
  },

  methods: {
    search(formhotel) {
      // console.log(formhotel.timerange)
      // console.log(formhotel.hotelname)
      formhotel=this.formhotel
      formhotel.validate((valid) => {
        if(valid)
        {
          axios.post('/back/hotel/queryhotelbysome',"hname="+this.formhotel.hotelname+"&keyword="+this.formhotel.keyword+"&rating="+this.formhotel.rating).then(function (response){
            console.log(response)
            if(response.status === 200)
            {
              console.log(response.data.result)
              if(response.data.result =="success"){
                router.push('/login')
              }
            }
          }).catch(failResponse => {}).catch(error => {
            console.log(error.response.data.code)
          })

        }
      });
    },

  }
}
</script>

<style   scoped>
.reserve-container{
  height: 100%;
}

.secend-header{
  background-color:#f9fafc;
  color: #333;
  text-align: center;
  line-height: 60px;

}
.first-header{
  display: flex;
  background: white;
  justify-content: space-between;
  /*padding-left:25% ;*/
  align-items: center;
}

.reserve-input1{
  display: flex;
  /*justify-content: center;*/
}
.reserve-input2{
  display: flex;
  /*justify-content: center;*/
}


</style>