import Vue from 'vue'
import Router from 'vue-router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import helloWorld from "@/components/HelloWorld";
Vue.use(Router)
const routes=[
    {
        path: '/',
        redirect: '/login'
    },
    {
        path: '/login',
        component:() =>  import('../pages/LoginBegin')
    },
    {
        path:'/main',
        component:() =>  import('../pages/MainInterface')
    }

    // {path: "",component:LoginBegin}
]

const router= new Router({
    // mode:'history',
    routes
})

export default router